import React from 'react';
import Popup from '../../../Popup/Popup.js';

function PersonCommunicationInfoPopup({ isOpen, onClose }) {

  function handleSubmit(e) {
    e.preventDefault();
    onClose();
  }

  return (
    <Popup
      isOpen={isOpen}
      onSubmit={handleSubmit}
      formWidth={'small'}
      formName={'person-communication-info-popup'}
    >
      <h2 className='popup__title'></h2>
      <p className='popup__subtitle'>    ,         :)</p>
      <p className='popup__text'><span className='popup__text-accent'>   </span>           .</p>
      <button className='popup__btn-back' type='submit'></button>
    </Popup>
  )
}

export default PersonCommunicationInfoPopup;